.nav {
    overflow: hidden;
    border-bottom: #0e2f55 2px solid;
  }

  .nav .box {
    max-width: 1100px;
    margin:0 auto;
    padding: 0;
    list-style-type:none;
    text-align: center;
}
  
  .nav a {
    float: left;
    text-align: center;
    padding: 14px 26px;
    font-size: 16px;
    text-decoration: none;
    text-transform: uppercase;
    font-weight: lighter;
    font-family: Arial,Helvetica Neue,Helvetica,sans-serif;
    font-weight: 400;
    color: #003035;
    -webkit-transition: all 0.5s ease-in-out;
    transition: all 0.5s ease-in-out;
  }
  
  .dropdown {
    float: left;
    overflow: hidden;
  }
  
  .dropdown .dropbtn {
    font-size: 16px; 
    text-transform: uppercase; 
    border: none;
    outline: none;
    color: #003035;
    padding: 14px 26px;
    background-color: inherit;
    margin: 0;
    cursor: pointer;
  }
  
  .nav a:hover, .dropdown:hover .dropbtn {
    color: #2163ad;
    -webkit-transition: all 0.2s ease-out;
    transition: all 0.2s ease-out;
  }
  
  .dropdown-content {
    display: none;
    position: absolute;
    background-color: #f9f9f9;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
  }
  
  .dropdown-content a {
    float: none;
    color: black;
    padding: 12px 26px;
    text-decoration: none;
    display: block;
    text-align: left;
  }
  
  .dropdown-content a:hover {
    background-color: rgb(182, 182, 182);
  }
  
  .dropdown:hover .dropdown-content {
    display: block;
  }
